import Axios from 'axios';
import React, { useEffect, useState } from 'react';
import {
  Container, Divider, Grid, Header,
} from 'semantic-ui-react';
import { compareObjects } from '../../../utils/utils';
import LoaderComponent from '../../Loader/Loader';
import ItemComponent from '../ItemComponent/ItemComponent';

const Cocktails = () => {
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    let mounted = true;
    setLoading(true);
    Axios({
      method: 'get',
      url: 'https://le-tdo.com/wp-json/wp/v2/les_cocktails',
      // headers: { Authorization: `Bearer ${token}` },
      params: {
        status: ['publish'],
        per_page: '50',
        order: 'asc',
      },
    })
      .then((response) => {
        if (mounted) {
          const AllItems = response.data;
          setItems(AllItems.sort((item1, item2) => compareObjects(item1, item2, 'meta')));
          setLoading(false);
        }
      });
    return function cleanup() {
      mounted = false;
    };
  }, []);
  return (
    <Container>
      <Header as="h1" textAlign="center" className="foodItems main__title">
        Cocktails
      </Header>
      <Divider />
      {loading === true && <LoaderComponent />}
      {loading === false
      && (
      <Grid columns={2}>
        {items.map((item) => (
          <ItemComponent key={item.id} item={item} />
        ))}
      </Grid>
      )}
    </Container>
  );
};

export default Cocktails;
