import Axios from 'axios';
import React, { useEffect, useState } from 'react';
import {
  Container, Divider, Grid, Header, Menu,
} from 'semantic-ui-react';
import { compareObjects, filterObjectsByCategory } from '../../../utils/utils';
import LoaderComponent from '../../Loader/Loader';
import ItemComponent from '../ItemComponent/ItemComponent';

import '../items.scss';

const Boissons = () => {
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(false);
  const [categories, setCategories] = useState('');
  const [itemsCategory, setItemsByCategory] = useState([]);

  useEffect(() => {
    let mounted = true;
    setLoading(true);
    Axios({
      method: 'get',
      url: 'https://le-tdo.com/wp-json/wp/v2/les_boissons',
      params: {
        status: ['publish'],
        per_page: '50',
        order: 'asc',
        orderby: 'title',
      },
    })
      .then((response) => {
        const AllItems = response.data;
        if (mounted) {
          setItems(AllItems.sort((item1, item2) => compareObjects(item1, item2, 'meta')));
          setItemsByCategory(AllItems.sort((item1, item2) => compareObjects(item1, item2, 'meta')));
          setLoading(false);
        }
      });
    return function cleanup() {
      mounted = false;
    };
  }, []);
  return (
    <Container>
      <Header as="h1" textAlign="center" className="foodItems main__title">
        Les Boissons
      </Header>
      <Divider />
      <Menu size="mini" compact widths={5} className="menu__categories">
        <Menu.Item
          name="toutes"
          active={categories === ''}
          onClick={() => {
            setCategories('');
            filterObjectsByCategory(items, '', setItemsByCategory);
          }}
        />
        <Menu.Item
          name="Alcools"
          active={categories === 'Alcools'}
          onClick={() => {
            setCategories('Alcools');
            filterObjectsByCategory(items, 'Alcools', setItemsByCategory);
          }}
        />
        <Menu.Item
          name="Spiritueux"
          active={categories === 'Spiritueux'}
          onClick={() => {
            setCategories('Spiritueux');
            filterObjectsByCategory(items, 'Spiritueux', setItemsByCategory);
          }}
        />
        <Menu.Item
          name="Bières"
          active={categories === 'Bières'}
          onClick={() => {
            setCategories('Bières');
            filterObjectsByCategory(items, 'Bières', setItemsByCategory);
          }}
        />
        <Menu.Item
          name="Divers"
          active={categories === 'Divers'}
          onClick={() => {
            setCategories('Divers');
            filterObjectsByCategory(items, 'zdivers', setItemsByCategory);
          }}
        />
      </Menu>
      <Divider />
      {loading === true && <LoaderComponent />}
      {loading === false
      && (
      <Grid columns={2}>
        {itemsCategory.map((item) => (
          <ItemComponent key={item.id} item={item} />
        ))}
      </Grid>
      )}
    </Container>
  );
};

export default Boissons;
