import Axios from 'axios';
import React, { useEffect, useState } from 'react';
import {
  Container, Divider, Grid, GridColumn, GridRow, Header, Menu,
} from 'semantic-ui-react';
import HeaderSubHeader from 'semantic-ui-react/dist/commonjs/elements/Header/HeaderSubheader';
import { compareObjects } from '../../../utils/utils';
import LoaderComponent from '../../Loader/Loader';

const Cuisine = () => {
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(false);
  const [tagliate, setTagliate] = useState([]);
  const [pasta, setPasta] = useState([]);
  const [antipasti, setAntipasti] = useState([]);
  const [carne, setCarne] = useState([]);

  useEffect(() => {
    let mounted = true;
    setLoading(true);
    const tagliateReq = Axios.get('https://le-tdo.com/wp-json/wp/v2/tagliate');
    const pastaReq = Axios.get('https://le-tdo.com/wp-json/wp/v2/ptes_et_risottos');
    const antipastiReq = Axios.get('https://le-tdo.com/wp-json/wp/v2/les_antipasti');
    const carneReq = Axios.get('https://le-tdo.com/wp-json/wp/v2/la_carne');
    Axios.all([tagliateReq, pastaReq, antipastiReq, carneReq])
      .then(Axios.spread((...responses) => {
        if (mounted) {
          setTagliate(responses[0].data);
          setPasta(responses[1].data);
          setAntipasti(responses[2].data);
          setCarne(responses[3].data);
          setLoading(false);
          setItems(responses[2].data);
        }
      }));
    return function cleanup() {
      mounted = false;
    };
  }, []);
  return (
    <Container>
      <Header as="h1" textAlign="center" className="foodItems main__title">
        La Cuisine
      </Header>
      <Divider />
      <Menu size="tiny" compact widths={4} className="menu__categories" activeIndex="1">
        <Menu.Item
          index={1}
          name="antipasti"
          active={items.type === 'antipasti'}
          onClick={() => {
            setItems(antipasti.sort((item1, item2) => compareObjects(item1, item2, 'meta')));
          }}
        />
        <Menu.Item
          name="tagliate"
          active={items.type === 'tagliates'}
          onClick={() => {
            setItems(tagliate.sort((item1, item2) => compareObjects(item1, item2, 'meta')));
          }}
        />
        <Menu.Item
          name="pasta"
          active={items.type === 'pasta e risotti'}
          onClick={() => {
            setItems(pasta.sort((item1, item2) => compareObjects(item1, item2, 'meta')));
          }}
        />
        <Menu.Item
          name="la carne"
          active={items.type === 'carne'}
          onClick={() => {
            setItems(carne.sort((item1, item2) => compareObjects(item1, item2, 'meta')));
          }}
        />
      </Menu>
      <Divider />
      {loading === true && <LoaderComponent />}
      {loading === false
      && (
      <Grid columns={2}>
        {items.map((item) => (
          <GridRow key={item.id}>
            <GridColumn>
              <Header as="h3" className="foodItems">
                {item.title.rendered}
                <HeaderSubHeader className="foodItems">
                  {item.content.rendered.replace(/(<([^>]+)>)/gi, '')}
                </HeaderSubHeader>
              </Header>
            </GridColumn>
            {item.meta.prix
            && (
            <GridColumn textAlign="right" className="foodItems">
              {item.meta.prix}€
            </GridColumn>
            )}
          </GridRow>
        ))}
      </Grid>
      )}
    </Container>
  );
};

export default Cuisine;
