import Axios from 'axios';
import React, { useEffect, useState } from 'react';
import {
  Container, Divider, Grid, Header, Menu,
} from 'semantic-ui-react';
import { compareObjects, filterObjectsByCategory } from '../../../utils/utils';
import LoaderComponent from '../../Loader/Loader';
import ItemComponent from '../ItemComponent/ItemComponent';

import '../items.scss';

const Cicchetteria = () => {
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(false);
  const [categories, setCategories] = useState('');
  const [itemsByCategory, setItemsByCategory] = useState([]);

  useEffect(() => {
    let mounted = true;
    setLoading(true);
    Axios({
      method: 'get',
      url: 'https://le-tdo.com/wp-json/wp/v2/cichetteria',
      params: {
        status: ['publish'],
        per_page: '50',
        order: 'asc',
        orderby: 'title',
      },
    })
      .then((response) => {
        const AllItems = response.data;
        if (mounted) {
          setItems(AllItems.sort((item1, item2) => compareObjects(item1, item2, 'meta')));
          setItemsByCategory(AllItems.sort((item1, item2) => compareObjects(item1, item2, 'meta')));
          setLoading(false);
        }
      });
    return function cleanup() {
      mounted = false;
    };
  }, []);
  return (
    <Container>
      <Header as="h1" textAlign="center" className="foodItems main__title">
        Cicchetteria
      </Header>
      <Divider />
      <Menu size="tiny" compact widths={3} className="menu__categories">
        <Menu.Item
          name="toutes"
          active={categories === ''}
          onClick={() => {
            setCategories('');
            filterObjectsByCategory(items, '', setItemsByCategory);
          }}
        />
        <Menu.Item
          name="A la carte ce soir"
          active={categories === 'A la carte de soir'}
          onClick={() => {
            setCategories('A la carte de soir');
            filterObjectsByCategory(items, 'A la carte de soir', setItemsByCategory);
          }}
        />
        <Menu.Item
          name="Le coin de la Truffe"
          active={categories === 'Le coin de la Truffe'}
          onClick={() => {
            setCategories('Le coin de la Truffe');
            filterObjectsByCategory(items, 'Le coin de la Truffe', setItemsByCategory);
          }}
        />
      </Menu>
      <Divider />
      {loading === true && <LoaderComponent />}
      {loading === false
      && (
      <Grid columns={2}>
        {itemsByCategory.map((item) => (
          <ItemComponent key={item.id} item={item} />
        ))}
      </Grid>
      )}
    </Container>
  );
};

export default Cicchetteria;
