import { faWineBottle } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Axios from 'axios';
import React, { useEffect, useState } from 'react';
import {
  Container, Divider, Grid, GridColumn, GridRow, Header, Menu,
} from 'semantic-ui-react';
import HeaderSubHeader from 'semantic-ui-react/dist/commonjs/elements/Header/HeaderSubheader';
import { compareObjects } from '../../../utils/utils';
import LoaderComponent from '../../Loader/Loader';

import '../items.scss';

const Vinotheque = () => {
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(false);
  const [categories, setCategories] = useState('');
  const [itemsCategory, setItemsByCategory] = useState([]);

  const filterObjectsByContenant = (allItems, cl) => {
    const newContenant = allItems.filter((item) => item.meta.contenant[0] === cl);
    setItemsByCategory(newContenant);
  };

  const filterObjectsByColor = (allItems, color) => {
    const newContenant = allItems.filter((item) => item.meta.Couleur.includes(color));
    setItemsByCategory(newContenant);
  };

  const filterObjectsByAll = (allItems) => {
    setItemsByCategory(allItems);
  };
  useEffect(() => {
    let mounted = true;
    setLoading(true);
    Axios({
      method: 'get',
      url: 'https://le-tdo.com/wp-json/wp/v2/vins',
      params: {
        status: ['publish'],
        per_page: '50',
        order: 'asc',
      },
    })
      .then((response) => {
        // @TODO filtrer aussi par noms
        const AllItems = response.data;
        if (mounted) {
          setItems(AllItems.sort((item1, item2) => compareObjects(item1, item2, 'meta')));
          setItemsByCategory(AllItems.sort((item1, item2) => compareObjects(item1, item2, 'meta')));
          setLoading(false);
        }
      });
    return function cleanup() {
      mounted = false;
    };
  }, []);
  return (
    <Container>
      <Header as="h1" textAlign="center" className="foodItems main__title">
        La Vinothèque
      </Header>
      <Divider />
      <Menu size="mini" compact widths={5} className="menu__categories">
        <Menu.Item
          name="toutes"
          active={categories === ''}
          onClick={() => {
            setCategories('');
            filterObjectsByAll(items);
          }}
        />
        <Menu.Item
          name="Rouge"
          active={categories === 'Rouge'}
          onClick={() => {
            setCategories('Rouge');
            filterObjectsByColor(items, 'Rouge');
          }}
        />
        <Menu.Item
          name="Blanc"
          active={categories === 'Blanc'}
          onClick={() => {
            setCategories('Blanc');
            filterObjectsByColor(items, 'Blanc');
          }}
        />
        <Menu.Item
          name="Rosé"
          active={categories === 'Rosé'}
          onClick={() => {
            setCategories('Rosé');
            filterObjectsByColor(items, 'Rosé');
          }}
        />
        <Menu.Item
          name="Les 50"
          active={categories === 'Les 50cl'}
          onClick={() => {
            setCategories('Les 50cl');
            filterObjectsByContenant(items, '50 cl');
          }}
        />
      </Menu>
      <Divider />
      {loading === true && <LoaderComponent />}
      {loading === false
      && (
      <Grid columns={2}>
        {itemsCategory.map((item) => (
          <GridRow key={item.id}>
            <GridColumn>
              <Header as="h3" className="foodItems">
                {item.title.rendered}
                <HeaderSubHeader className="foodItems">
                  {item.content.rendered.replace(/(<([^>]+)>)/gi, '')}
                </HeaderSubHeader>
              </Header>
              {item.meta.contenant !== undefined && (
              <span style={{ color: 'white', display: 'block' }}>
                {item.meta.contenant}
              </span>
              )}
              {item.meta.Couleur !== undefined && (
              <span>
                {item.meta.Couleur.includes('Rouge') && <FontAwesomeIcon icon={faWineBottle} style={{ color: '#C10501' }} size="lg" />}
                {item.meta.Couleur.includes('Blanc') && <FontAwesomeIcon icon={faWineBottle} style={{ color: '#E8EA8D' }} size="lg" />}
                {item.meta.Couleur.includes('Rosé') && <FontAwesomeIcon icon={faWineBottle} style={{ color: '#CF6021' }} size="lg" />}
              </span>
              )}
            </GridColumn>
            <GridColumn textAlign="right" className="foodItems">
              {item.meta.prix}€
            </GridColumn>
          </GridRow>
        ))}
      </Grid>
      )}
    </Container>
  );
};

export default Vinotheque;
