import Axios from 'axios';
import React, { useEffect, useState } from 'react';
import {
  Container, Divider, Grid, Header, Menu,
} from 'semantic-ui-react';
import { compareObjects, filterObjectsByCategory } from '../../../utils/utils';
import LoaderComponent from '../../Loader/Loader';
import ItemComponent from '../ItemComponent/ItemComponent';

import '../items.scss';

const Pizze = () => {
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(false);
  const [categories, setCategories] = useState('');
  const [itemsByCategory, setItemsByCategory] = useState([]);

  useEffect(() => {
    let mounted = true;
    setLoading(true);
    Axios({
      method: 'get',
      url: 'https://le-tdo.com/wp-json/wp/v2/pizzas',
      // headers: { Authorization: `Bearer ${token}` },
      params: {
        status: ['publish'],
        per_page: '50',
        order: 'asc',
      },
    })
      .then((response) => {
        const AllItems = response.data;
        if (mounted) {
          setItems(AllItems.sort((item1, item2) => compareObjects(item1, item2, 'meta')));
          setItemsByCategory(AllItems.sort((item1, item2) => compareObjects(item1, item2, 'meta')));
          setLoading(false);
        }
      });
    return function cleanup() {
      mounted = false;
    };
  }, []);

  return (
    <Container>
      <Header as="h1" textAlign="center" className="foodItems main__title">
        Pizze al Forno
      </Header>
      <Divider />
      <Menu size="tiny" compact widths={3} className="menu__categories">
        <Menu.Item
          name="toutes"
          active={categories === ''}
          onClick={() => {
            setCategories('');
            filterObjectsByCategory(items, '', setItemsByCategory);
          }}
        />
        <Menu.Item
          name="Les crèatives"
          active={categories === 'les créatives'}
          onClick={() => {
            setCategories('Les créatives');
            filterObjectsByCategory(items, 'Les créatives', setItemsByCategory);
          }}
        />
        <Menu.Item
          name="les classiques"
          active={categories === 'Les classiques'}
          onClick={() => {
            setCategories('Les classiques');
            filterObjectsByCategory(items, 'Les classiques', setItemsByCategory);
          }}
        />
      </Menu>
      <Divider />
      {loading === true && <LoaderComponent />}
      {loading === false
      && (
      <Grid columns={2}>
        {itemsByCategory.map((item) => (
          <ItemComponent key={item.id} item={item} />
        ))}
      </Grid>
      )}
    </Container>
  );
};

export default Pizze;
